
























import Vue from "vue";
import Component from "vue-class-component";
import { AppService } from "../service/app.service";

@Component({})
export default class Login extends Vue {
  form: any = {};
  app = AppService.instance;
  init = false;

  async onSubmit(): Promise<void> {
    // console.log("Login::form", this.form);
    try {
      await this.app.api.login(this.form);
      // console.log('Login::success - ', user);
      this.$router.push("/").catch(() => null);
    } catch (e) {
      this.app.error(e);
    }
  }
  loginWithKakao(): void {
    if (this.init === false) {
      (window as any).Kakao.init("937af10cf8688bd9a7554cf088b2ac3e");
      this.init = true;
    }
    (window as any).Kakao.Auth.login({
      success: function (authObj: any) {
        alert(JSON.stringify(authObj));
      },
      fail: function (err: any) {
        alert(JSON.stringify(err));
      },
    });
  }
}
